@font-face {
	font-family: swiper-icons;
	src: url('data:application/font-woff;charset=utf-8;base64, ') format("woff");
	font-weight: 400;
	font-style: normal;
 }
 
 :root {
	--swiper-theme-color:#007aff;
 }
 
 .swiper-container {
	margin-left: auto;
	margin-right: auto;
	position: relative;
	overflow: hidden;
	list-style: none;
	padding: 0;
	z-index: 1;
 }
 
 .swiper-container-vertical > .swiper-wrapper {
	flex-direction: column;
 }
 
 .swiper-wrapper {
	position: relative;
	width: 100%;
	height: 100%;
	z-index: 1;
	display: flex;
	transition-property: transform;
	box-sizing: content-box;
 }
 
 .swiper-container-android .swiper-slide, .swiper-wrapper {
	transform: translate3d(0px, 0, 0);
 }
 
 .swiper-container-multirow > .swiper-wrapper {
	flex-wrap: wrap;
 }
 
 .swiper-container-multirow-column > .swiper-wrapper {
	flex-wrap: wrap;
	flex-direction: column;
 }
 
 .swiper-container-free-mode > .swiper-wrapper {
	transition-timing-function: ease-out;
	margin: 0 auto;
 }
 
 .swiper-container-pointer-events {
	touch-action: pan-y;
 
	&.swiper-container-vertical {
	  touch-action: pan-x;
	}
 }
 
 .swiper-slide {
	flex-shrink: 0;
	width: 100%;
	height: 100%;
	position: relative;
	transition-property: transform;
 }
 
 .swiper-slide-invisible-blank {
	visibility: hidden;
 }
 
 .swiper-container-autoheight {
	height: auto;
 
	.swiper-slide {
	  height: auto;
	}
 
	.swiper-wrapper {
	  align-items: flex-start;
	  transition-property: transform,height;
	}
 }
 
 .swiper-container-3d {
	perspective: 1200px;
 
	.swiper-cube-shadow, .swiper-slide, .swiper-slide-shadow-bottom, .swiper-slide-shadow-left, .swiper-slide-shadow-right, .swiper-slide-shadow-top, .swiper-wrapper {
	  transform-style: preserve-3d;
	}
 
	.swiper-slide-shadow-bottom, .swiper-slide-shadow-left, .swiper-slide-shadow-right, .swiper-slide-shadow-top {
	  position: absolute;
	  left: 0;
	  top: 0;
	  width: 100%;
	  height: 100%;
	  pointer-events: none;
	  z-index: 10;
	}
 
	.swiper-slide-shadow-left {
	  background-image: linear-gradient(to left, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
	}
 
	.swiper-slide-shadow-right {
	  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
	}
 
	.swiper-slide-shadow-top {
	  background-image: linear-gradient(to top, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
	}
 
	.swiper-slide-shadow-bottom {
	  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
	}
 }
 
 .swiper-container-css-mode > .swiper-wrapper {
	overflow: auto;
	scrollbar-width: none;
 
	&::-webkit-scrollbar {
	  display: none;
	}
 
	> .swiper-slide {
	  scroll-snap-align: start start;
	}
 }
 
 .swiper-container-horizontal.swiper-container-css-mode > .swiper-wrapper {
	scroll-snap-type: x mandatory;
 }
 
 .swiper-container-vertical.swiper-container-css-mode > .swiper-wrapper {
	scroll-snap-type: y mandatory;
 }
 
 :root {
	--swiper-navigation-size:44px;
 }
 
 .swiper-button-next, .swiper-button-prev {
	position: absolute;
	top:91%;
	width: calc(var(--swiper-navigation-size)/ 44 * 27);
	height: var(--swiper-navigation-size);
	margin-top: calc(0px - (var(--swiper-navigation-size)/ 2));
	z-index: 10;
	cursor: pointer;
	display: flex;
	align-items: center;
	justify-content: center;
	color: var(--swiper-navigation-color, var(--swiper-theme-color));
 }
 
 .swiper-button-next.swiper-button-disabled, .swiper-button-prev.swiper-button-disabled {
	opacity: .35;
	cursor: auto;
	pointer-events: none;
 }
 
 .swiper-button-next:after {
	font-family: swiper-icons;
	font-size: var(--swiper-navigation-size);
	text-transform: none !important;
	letter-spacing: 0;
	text-transform: none;
	font-variant: initial;
	line-height: 1;
 }
 
 .swiper-button-prev {
	&:after {
	  font-family: swiper-icons;
	  font-size: var(--swiper-navigation-size);
	  text-transform: none !important;
	  letter-spacing: 0;
	  text-transform: none;
	  font-variant: initial;
	  line-height: 1;
	}
 
	left: 10px;
	right: auto;
 }
 
 .swiper-container-rtl .swiper-button-next {
	left: 10px;
	right: auto;
 }
 

 
 .swiper-button-next, .swiper-container-rtl .swiper-button-prev {
	right: 0;
	left: auto;
 }



 .swiper-button-prev,
 .swiper-button-next {
	width: 48px;
	height: 48px;
	fill: #473534;
	transform: rotate(180deg);
	transition: all 500ms ease;
 }
 .swiper-button-prev:hover,
 .swiper-button-next:hover{
	background-color:#888 ;
	fill: tomato;
 }
 .swiper-button-prev{
	 right: 0;
	transform: rotate(0deg);
 }
 .swiper__buttons{
	 position: absolute;
	 bottom: 25px;
	 right: 0;
	 width: 115px;
		@include media(max,$sm){
			bottom: 50px;
		}
 }

 
 .swiper-button-next.swiper-button-white, .swiper-button-prev.swiper-button-white {
	--swiper-navigation-color:#ffffff;
 }
 
 .swiper-button-next.swiper-button-black, .swiper-button-prev.swiper-button-black {
	--swiper-navigation-color:#000000;
 }
 
 .swiper-button-lock {
	display: none;
 }
 
 .swiper-pagination {
	position: absolute;
	text-align: center;
	transition: .3s opacity;
	transform: translate3d(0, 0, 0);
	z-index: 10;
 
	&.swiper-pagination-hidden {
	  opacity: 0;
	}
 }
 
 .swiper-container-horizontal > .swiper-pagination-bullets, .swiper-pagination-custom, .swiper-pagination-fraction {
	bottom: 10px;
	left: 0;
	width: 100%;
 }
 
 .swiper-pagination-bullets-dynamic {
	overflow: hidden;
	font-size: 0;
 
	.swiper-pagination-bullet {
	  transform: scale(0.33);
	  position: relative;
	}
 
	.swiper-pagination-bullet-active, .swiper-pagination-bullet-active-main {
	  transform: scale(1);
	}
 
	.swiper-pagination-bullet-active-prev {
	  transform: scale(0.66);
	}
 
	.swiper-pagination-bullet-active-prev-prev {
	  transform: scale(0.33);
	}
 
	.swiper-pagination-bullet-active-next {
	  transform: scale(0.66);
	}
 
	.swiper-pagination-bullet-active-next-next {
	  transform: scale(0.33);
	}
 }
 
 .swiper-pagination-bullet {
	width: 8px;
	height: 8px;
	display: inline-block;
	border-radius: 50%;
	background: #000;
	opacity: .2;
 }
 
 button.swiper-pagination-bullet {
	border: none;
	margin: 0;
	padding: 0;
	box-shadow: none;
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
 }
 
 .swiper-pagination-clickable .swiper-pagination-bullet {
	cursor: pointer;
 }
 
 .swiper-pagination-bullet-active {
	opacity: 1;
	background: var(--swiper-pagination-color, var(--swiper-theme-color));
 }
 
 .swiper-container-vertical > .swiper-pagination-bullets {
	right: 10px;
	top: 50%;
	transform: translate3d(0px, -50%, 0);
 
	.swiper-pagination-bullet {
	  margin: 6px 0;
	  display: block;
	}
 
	&.swiper-pagination-bullets-dynamic {
	  top: 50%;
	  transform: translateY(-50%);
	  width: 8px;
 
	  .swiper-pagination-bullet {
		 display: inline-block;
		 transition: .2s transform,.2s top;
	  }
	}
 }
 
 .swiper-container-horizontal {
	> .swiper-pagination-bullets {
	  .swiper-pagination-bullet {
		 margin: 0 4px;
	  }
 
	  &.swiper-pagination-bullets-dynamic {
		 left: 50%;
		 transform: translateX(-50%);
		 white-space: nowrap;
 
		 .swiper-pagination-bullet {
			transition: .2s transform,.2s left;
		 }
	  }
	}
 
	&.swiper-container-rtl > .swiper-pagination-bullets-dynamic .swiper-pagination-bullet {
	  transition: .2s transform,.2s right;
	}
 }
 
 .swiper-pagination-progressbar {
	background: rgba(0, 0, 0, 0.25);
	position: absolute;
 
	.swiper-pagination-progressbar-fill {
	  background: var(--swiper-pagination-color, var(--swiper-theme-color));
	  position: absolute;
	  left: 0;
	  top: 0;
	  width: 100%;
	  height: 100%;
	  transform: scale(0);
	  transform-origin: left top;
	}
 }
 
 .swiper-container-rtl .swiper-pagination-progressbar .swiper-pagination-progressbar-fill {
	transform-origin: right top;
 }
 
 .swiper-container-horizontal > .swiper-pagination-progressbar, .swiper-container-vertical > .swiper-pagination-progressbar.swiper-pagination-progressbar-opposite {
	width: 100%;
	height: 4px;
	left: 0;
	top: 0;
 }
 
 .swiper-container-horizontal > .swiper-pagination-progressbar.swiper-pagination-progressbar-opposite, .swiper-container-vertical > .swiper-pagination-progressbar {
	width: 4px;
	height: 100%;
	left: 0;
	top: 0;
 }
 
 .swiper-pagination-white {
	--swiper-pagination-color:#ffffff;
 }
 
 .swiper-pagination-black {
	--swiper-pagination-color:#000000;
 }
 
 .swiper-pagination-lock {
	display: none;
 }
 
 .swiper-scrollbar {
	border-radius: 10px;
	position: relative;
	-ms-touch-action: none;
	background: rgba(0, 0, 0, 0.1);
 }
 
 .swiper-container-horizontal > .swiper-scrollbar {
	position: absolute;
	left: 1%;
	bottom: 3px;
	z-index: 50;
	height: 5px;
	width: 98%;
 }
 
 .swiper-container-vertical > .swiper-scrollbar {
	position: absolute;
	right: 3px;
	top: 1%;
	z-index: 50;
	width: 5px;
	height: 98%;
 }
 
 .swiper-scrollbar-drag {
	height: 100%;
	width: 100%;
	position: relative;
	background: rgba(0, 0, 0, 0.5);
	border-radius: 10px;
	left: 0;
	top: 0;
 }
 
 .swiper-scrollbar-cursor-drag {
	cursor: move;
 }
 
 .swiper-scrollbar-lock {
	display: none;
 }
 
 .swiper-zoom-container {
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	text-align: center;
 
	> {
	  canvas, img, svg {
		 max-width: 100%;
		 max-height: 100%;
		 object-fit: contain;
	  }
	}
 }
 
 .swiper-slide-zoomed {
	cursor: move;
 }
 
 .swiper-lazy-preloader {
	width: 42px;
	height: 42px;
	position: absolute;
	left: 50%;
	top: 50%;
	margin-left: -21px;
	margin-top: -21px;
	z-index: 10;
	transform-origin: 50%;
	animation: swiper-preloader-spin 1s infinite linear;
	box-sizing: border-box;
	border: 4px solid var(--swiper-preloader-color, var(--swiper-theme-color));
	border-radius: 50%;
	border-top-color: transparent;
 }
 
 .swiper-lazy-preloader-white {
	--swiper-preloader-color:#fff;
 }
 
 .swiper-lazy-preloader-black {
	--swiper-preloader-color:#000;
 }
 
 @keyframes swiper-preloader-spin {
	100% {
	  transform: rotate(360deg);
	}
 }
 
 .swiper-container .swiper-notification {
	position: absolute;
	left: 0;
	top: 0;
	pointer-events: none;
	opacity: 0;
	z-index: -1000;
 }
 
 .swiper-container-fade {
	&.swiper-container-free-mode .swiper-slide {
	  transition-timing-function: ease-out;
	}
 
	.swiper-slide {
	  pointer-events: none;
	  transition-property: opacity;
 
	  .swiper-slide {
		 pointer-events: none;
	  }
	}
 
	.swiper-slide-active {
	  pointer-events: auto;
 
	  .swiper-slide-active {
		 pointer-events: auto;
	  }
	}
 }
 
 .swiper-container-cube {
	overflow: visible;
 
	.swiper-slide {
	  pointer-events: none;
	  backface-visibility: hidden;
	  z-index: 1;
	  visibility: hidden;
	  transform-origin: 0 0;
	  width: 100%;
	  height: 100%;
 
	  .swiper-slide {
		 pointer-events: none;
	  }
	}
 
	&.swiper-container-rtl .swiper-slide {
	  transform-origin: 100% 0;
	}
 
	.swiper-slide-active {
	  pointer-events: auto;
 
	  .swiper-slide-active {
		 pointer-events: auto;
	  }
 
	  pointer-events: auto;
	  visibility: visible;
	}
 
	.swiper-slide-next {
	  pointer-events: auto;
	  visibility: visible;
 
	  + .swiper-slide {
		 pointer-events: auto;
		 visibility: visible;
	  }
	}
 
	.swiper-slide-prev {
	  pointer-events: auto;
	  visibility: visible;
	}
 
	.swiper-slide-shadow-bottom, .swiper-slide-shadow-left, .swiper-slide-shadow-right, .swiper-slide-shadow-top {
	  z-index: 0;
	  backface-visibility: hidden;
	}
 
	.swiper-cube-shadow {
	  position: absolute;
	  left: 0;
	  bottom: 0px;
	  width: 100%;
	  height: 100%;
	  opacity: .6;
	  z-index: 0;
 
	  &:before {
		 content: '';
		 background: #000;
		 position: absolute;
		 left: 0;
		 top: 0;
		 bottom: 0;
		 right: 0;
		 filter: blur(50px);
	  }
	}
 }
 
 .swiper-container-flip {
	overflow: visible;
 
	.swiper-slide {
	  pointer-events: none;
	  backface-visibility: hidden;
	  z-index: 1;
 
	  .swiper-slide {
		 pointer-events: none;
	  }
	}
 
	.swiper-slide-active {
	  pointer-events: auto;
 
	  .swiper-slide-active {
		 pointer-events: auto;
	  }
	}
 
	.swiper-slide-shadow-bottom, .swiper-slide-shadow-left, .swiper-slide-shadow-right, .swiper-slide-shadow-top {
	  z-index: 0;
	  backface-visibility: hidden;
	}
 }

